<template lang="html">

  <el-row class="video_tool" >
    <el-col class="tag" :span="6">
      <el-tag
        type="success"
        hit
        :key="tag"
        v-for="tag in videoData.tag">
          {{tag}}
        </el-tag>
        <el-input
          hidden-md-only
          class="input-new-tag"
          v-if="inputVisible"
          v-model.trim="newTag"
          ref="saveTagInput"
          size="small"
          @keyup.enter.native="set_tag"
        >
        </el-input>
        <el-button v-else class="button-new-tag" size="small" @click="showInput">+ New Tag</el-button>
    </el-col>

    <el-col
      :span="6"
      class="rate-box">
        <span>评分</span>
      <el-rate
        v-model="score"
        allow-half
        class="rate"
        @change="setScore()"
        void-color="#aaa"
        :disabled="disabled"></el-rate>
    </el-col>

    <el-col :span="2" class="delete_btn">
      <el-button type="danger" icon="el-icon-delete"
      :disabled="deleted" @click="delete_video"></el-button>
    </el-col>

    <el-col :span="3" class="fix_btn">
      <el-button type="warning"
      @click="set_encode_wrong" icon="el-icon-setting">
      </el-button>
    </el-col>
  </el-row>
</template>

<script>
import 'element-ui/lib/theme-chalk/display.css';
export default {
  props: [
    'id'
  ],
  data() {
    return {
      tags: [],
      score: 0,
      newTag: '',
      inputVisible: false,
      videoData:{}
    }
  },
  computed: {
    deleted() {
      if (this.videoData.deleted) {
        return true
      } else {
        return false
      }
    },
    disabled() {
      if (this.videoData.score) {
        return
      }
    }
  },
  methods: {

    showInput() {
      this.inputVisible = true;
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus();
      });
    },

    get_video_data(){
      this.$http.get('/video/videoData',{
        params:{
          id:this.id
        }
      }).then((res)=>{
        this.videoData = res.data
      })
    },

    setScore() {
      let params = new URLSearchParams()
      params.append('score', `${this.score}`)
      params.append('id', `${this.videoData.id}`)
      this.disabled = true
      this.$http.post('/video/setScore', params, {
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then((res) => {
        this.disabled = true
      })
    },

    set_encode_wrong(){
      let params = new URLSearchParams()
      params.append('id', `${this.id}`)
      this.$http.post('/video/encodeWrong',params,{
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then((res)=>{
        this.$message('移出播放列表')
        this.$router.push('/')
      })
    },

    delete_video() {
      let params = new URLSearchParams()
      params.append('id', `${this.id}`)
      this.$http.post('/video/setDelete', params, {
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then((res) => {
        if (res.data.status === 1) {
          this.$message('删除成功')
          this.$router.push('/')
        }
      })
    },

    set_tag() {
      if(!this.newTag||this.videoData.tag.indexOf(this.newTag)>=0){
        this.inputVisible = false
        this.newTag = ''
        return
      }
      let params = new URLSearchParams()
      params.append('id', `${this.id}`)
      params.append('tag', `${this.newTag}`)
      this.$http.post('/video/addTag', params, {
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then((res) => {
        this.videoData.tag = res.data
        this.inputVisible = false
      })
    }
  },
  mounted() {
    this.get_video_data()
    if(this.videoData.score){
      this.score = this.videoData.score
    }
  },

}
</script>

<style lang="css">
.video_tool{
  margin-top: 1rem;
  width: 100%;
  padding: 0 1.25rem;
}
.rate-box{

}
.rate{
  display: inline;
}
.button-new-tag {
  margin-left: 10px;
  height: 32px;
  line-height: 30px;
  padding-top: 0;
  padding-bottom: 0;
}
.input-new-tag {
  width: 90px;
  margin-left: 10px;
  vertical-align: bottom;
}
.delete_btn{
  float: right;
}
.fix_btn{
  float:right;
}
</style>



